@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
.movies .movie {
  display: -webkit-flex;
  display: flex;
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  border-radius: 5px;
}
.movie img {
  position: relative;
  top: -35px;
  max-width: 150px;
  width: 100%;
  margin-right: 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.movie .movie__title,
.movie .movie__year {
  font-weight: 400;
  margin: 0;
}
.movie .movie__title {
  margin-bottom: 5px;
  font-size: 23px;
}
.movie .movie__year,
.movie .movie__genres {
  font-size: 12px;
  opacity: 0.5;
}
.movie .move_summary {
  font-size: 15px;
}
.movie .movie__genres {
  list-style: none;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  font-size: 15px;
  margin: 5px 0px;
}
.movie__genres li {
  margin-right: 10px;
}

* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  height: 100%;
}
html,
body,
.container {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: "Montserrat", sans-serif;
}
.loader {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.movies {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 60%;
}

